
import { mapState } from 'vuex'

export default {
  name: 'IndexPage',
  middleware: 'auth',
  data () {
    return {
      searchKey: ''
    }
  },
  head () {
    return {
      title: `ðŸ“ž Jambo Talk - Home (${this.creds.username})`
    }
  },
  computed: {
    ...mapState(['contacts', 'creds']),
    searchResults () {
      let tempContacts = [...this.contacts]
      if (this.searchKey !== '' && this.searchKey) {
        tempContacts = tempContacts.filter(({ name, tel }) => {
          const value = `${name} ${tel}`
          return value.toUpperCase()
            .includes(this.searchKey.toUpperCase())
        })
      }
      return tempContacts.sort((a, b) => a.name.localeCompare(b.name))
    }
  },
  mounted () {
    this.$nuxt.$emit('safe_auth') // Get Server Token
  },
  beforeDestroy () {
    this.$nuxt.$off('contactsFound')
  }
}
